<template>
  <div class="dialogBox">
    <el-dialog title="修改基本信息" :visible.sync="dialogFormVisible" width="60%">
      <el-form :model="stuForm">
        <el-form-item label="学号" :label-width="formLabelWidth">
          <el-input v-model="stuForm.stuNo" autocomplete="off" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="姓名" :label-width="formLabelWidth">
          <el-input v-model="stuForm.stuName" autocomplete="off" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="身份证" :label-width="formLabelWidth">
          <el-input v-model="stuForm.idCard" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="性别" :label-width="formLabelWidth">
          <el-select v-model="stuForm.stuSex" placeholder="请选择你的性别">
            <el-option label="男" value="1"></el-option>
            <el-option label="女" value="2"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="政治面貌" :label-width="formLabelWidth">
          <el-select v-model="stuForm.politicalStatus" placeholder="请选择你的政治面貌">
            <el-option label="党员" value="1"></el-option>
            <el-option label="预备党员" value="2"></el-option>
            <el-option label="共青团员" value="3"></el-option>
            <el-option label="群众" value="4"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="学历" :label-width="formLabelWidth">
          <el-select v-model="stuForm.education" placeholder="请选择你的学历">
            <el-option label="本科" value="1"></el-option>
            <el-option label="博士" value="2"></el-option>
            <el-option label="硕士" value="3"></el-option>
            <el-option label="高中" value="4"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="联系电话" :label-width="formLabelWidth">
          <el-input v-model="stuForm.stuTel" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="院系" :label-width="formLabelWidth">
          <el-select v-model="stuForm.departmentKey" placeholder="请选择你的学院" @change="changeDepart">
            <el-option
              v-for="(item,index) in departmentList"
              :key="index"
              :label="item.name"
              :value="item.key"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="专业" :label-width="formLabelWidth">
          <el-select
            v-model="stuForm.professionKey"
            placeholder="请选择你的专业"
            @change="changeProfssion"
          >
            <el-option
              v-for="(item,index) in professionList"
              :key="index"
              :label="item.name"
              :value="item.key"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="班级" :label-width="formLabelWidth">
          <el-select v-model="stuForm.classNo" placeholder="请选择你的班级">
            <el-option
              v-for="(item,index) in classList"
              :key="index"
              :label="item.name"
              :value="item.key"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="毕业时间" :label-width="formLabelWidth">
          <el-date-picker
            v-model="stuForm.graduationTime"
            type="date"
            placeholder="选择毕业时间"
            value-format="yyyy-MM-dd"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="生源地" :label-width="formLabelWidth">
          <el-input v-model="stuForm.biogenicLand" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="家庭联系人" :label-width="formLabelWidth">
          <el-input v-model="stuForm.familyContact" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="家庭联系电话" :label-width="formLabelWidth">
          <el-input v-model="stuForm.homeTel" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="家庭住址" :label-width="formLabelWidth">
          <el-input v-model="stuForm.homeAddress" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="submitForm('stuForm')">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import {
  updateStuInfo,
  getProfessionAttach 
} from "../../../../../../utils/employment.url";
import { isNum } from "@/utils/utils";
export default {
  props: ["dialogFormVisible", "stuForm"],
  data() {
    return {
      departmentList: [], //学院列表
      professionList: [], //专业列表
      classList: [], //班级列表
      attachList: [], //院系信息列表
      // presentTitle:"添加就业代表信息",
      formLabelWidth: "120px",
      fileList: [],
      rules: {
        userId: [
          { required: true, message: "请输入学号/教工号", trigger: "blur" }
        ],
        userName: [{ required: true, message: "请输入姓名", trigger: "blur" }],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }],
        userType: [
          { required: true, message: "请选择用户类型", trigger: "blur" }
        ],
        session: [
          { required: true, message: "请选择用户所属年份", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    dialogClose() {
      this.$emit("dialogClose", false);
    },
    //,取消
    cancelEdit() {
      this.$emit("dialogClose", false);
    },
    submitForm(formName) {
      // this.$refs[formName].validate(valid => {
      //   if (valid) {
          //修改学生基本信息
          updateStuInfo(this.stuForm, "put").then(res => {
            if (res.status == 200) {
              this.$emit("dialogClose", false);
              this.$message({
                message: res.message,
                type: "success"
              });
            }
          });
      //   } else {
      //     return false;
      //   }
      // });
    },
    getProfessionAttach(session, departmentKey, professionKey, classKey) {
      var params = {
        session: session ? session : "",
        departmentKey: departmentKey ? departmentKey : "",
        professionKey: professionKey ? professionKey : "",
        classKey: classKey ? classKey : ""
      };
      getProfessionAttach(params, "get").then(res => {
        if (res.status == 200) {
          this.attachList = res.data.attachList;
          this.departmentList = this.groupBy(this.attachList, function(item) {
            return [item.departmentKey, item.departmentName]; //按照时间年月分组
          });
        }
      });
    },
    //数组对象分组
    groupBy(array, f) {
      var groups = {};
      array.forEach(function(o) {
        var group = JSON.stringify(f(o));
        groups[group] = groups[group] || [];
        groups[group].push(o);
      });
      return Object.keys(groups).map(function(group) {
        var _group =JSON.parse(group)
        return {
          key:_group[0],
          name:_group[1],
          child:groups[group]
        };
      });
    },
    changeDepart(key){
      this.stuForm.professionKey='',
      this.departmentList.map((item,index) =>{
        if(item.key == key){
          this.professionList = this.groupBy(item.child, function(item) {
            return [item.professionKey, item.professionName]; //按照时间年月分组
          });
          // console.log(this.professionList)
        }
      })
    },
    changeProfssion(key){
      this.professionList.map((item,index) =>{
        if(item.key == key){
          this.classList = this.groupBy(item.child, function(item) {
            return [item.classKey, item.className]; //按照时间年月分组
          });
        }
      })
    }
    
  },
  created() {
    let session = "";
    let departmentKey = "";
    let professionKey = "";
    let classKey = "";
    this.getProfessionAttach(session, departmentKey, professionKey, classKey);
  }
};
</script>
<style lang="scss" scoped>
