import {
    getStudentList,
    deleteStudentBasicInfo
} from "../../../../../../utils/employment.url";
import studentDialog from "./../commonModel/studentDialog.vue";
export default {
    components: {
        studentDialog
    },
    data() {
        return {
            input: '',
            studentList: [], //用户列表
            search: '',
            pageObj: {
                pageRowNum: 8,
                currPage: 1,
                totalRow: 20,
            },
            dialogFormVisible: false,
            editstudentInfo: {},
        }
    },
    methods: {
        getStudentList(pageObj) {
            getStudentList(pageObj, "get").then(res => {
                if (res.status == 200) {
                    this.studentList = res.data.studentList;
                    this.pageObj = res.data.pageObj;
                    this.studentList.map((item,index) =>{
                        item.graduationTime = item.graduationTime.split(
                            "T"
                          )[0];
                    })
                }
            })
        },
        changePage(val) {
            this.pageObj.currPage = val;
            this.getStudentList(this.pageObj);
        },
        dialogClose(value) {
            this.$store.commit('changeBasicStatus', false);
            this.dialogFormVisible = value;
            this.getStudentList(this.pageObj);
        },
        handleEdit(index, row) {
            // console.log(row)
            this.editstudentInfo = row;
            this.dialogFormVisible = true;
            this.$store.commit('changeBasicStatus', true);
        },
        handleDelete(index, row) {
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                deleteStudentBasicInfo(row, "delete").then(res => {
                    if (res.status == 200) {
                        this.getStudentList(this.pageObj);
                        this.$message({
                            message: res.message,
                            type: "success"
                        });
                    }
                });
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
            });
        },
        getValue(key, type) {
            let value = ""
            if (type == 1) {
                switch (key) {
                    case "1":
                        value = "本科";
                        break;
                    case "2":
                        value = "博士";
                        break;
                    case "3":
                        value = "硕士";
                        break;
                    case "4":
                        value = "高中";
                        break;
                    default:
                        break;
                }
                return value
            } else {
                switch (key) {
                    case "1":
                        value = "党员";
                        break;
                    case "2":
                        value = "预备党员";
                        break;
                    case "3":
                        value = "共青团员";
                        break;
                    case "4":
                        value = "群众";
                        break;
                    default:
                        break;
                }
                return value
            }
        }
    },
    created() {
        this.getStudentList(this.pageObj);
        console.log(this.getValue(2, 1))
    }
}